/* Basic Reset */
*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  font-family: System, monospace;
  font-style: bold;
  background-color: #00a7a7; 
  color: black;
}

.header {
    display: flex; 
    align-items: center; 
    justify-content: start; 
    padding: 10px; 
  }
  
  .header img {
    max-width: 250px; 
    max-height: 250px;  
    object-fit: cover;  
    margin-right: 20px; 
  }
  
  .header-info {
    display: flex;
    flex-direction: column; /* Stacks the text items vertically */
  }

     /* Responsive adjustments */
     @media (max-width: 768px) {
      .header {
        flex-direction: column;
        text-align: center;
      }
    
      .header img {
        width: 100px; /* Adjust for smaller screens */
        height: 100px; /* Adjust to maintain aspect ratio */
        margin-right: 0; /* Remove margin on the right for centered image */
        margin-bottom: 20px; /* Add some space below the image */
      }
    
      .header-info {
        margin-left: 0; /* Reset margin for smaller screens */
      }
    }


.top-menu-bar {
    top: 0;
    left: 0;
    right: 0;
    background: silver;
    font-weight: bolder;
    text-align: center;
}

/* Classic Windows 95 window */
.window {
    display: flex;
    position: relative;
    cursor: default;
    margin: 2em auto;
    width: 80%;
    background: silver;
    color: #181818;
    border: 6px double #fff;
    padding: 1em;
  box-shadow: 0 0 0 3px #909092, 1em 1em 3px 0 rgba(0,0,0,.5);
}

.window-titlebar {
  background-color: #000080; /* Dark blue */
  color: white;
  padding: 4px;
}

/* 3D Button */
.button {
  background-color: #c0c0c0;
  border-top: 2px solid #ffffff; /* White top border for 3D effect */
  border-left: 2px solid #ffffff;
  border-bottom: 2px solid #808080; /* Dark border bottom for 3D effect */
  border-right: 2px solid #808080;
  padding: 5px 15px;
  font-family: 'MS Sans Serif', Arial;
  cursor: pointer;
}

.button:active {
  border-top: 2px solid #808080; /* Invert for pressed effect */
  border-left: 2px solid #808080;
  border-bottom: 2px solid #ffffff;
  border-right: 2px solid #ffffff;
}
